/*
* Style Works for Editor Only
*/
.aa-custom-spacer {
	width: 100%;
	height: 1px;
	background: #e3e3e3;
	margin: 16px 0;
}

.wp-block-aab-accordion-item {
	.aagb__accordion_body {
		display: block;
		&.aagb__accordion_body--show {
			display: block;
		}
	}
}


.aagb_form_control{
	color: #848d95 !important;
}

.wp-block-aab-accordion-item .aagb__accordion_body .aagb__accordion_component .block-editor-block-list__layout p{
	margin-top: 0 !important;
	margin-bottom: 20px;

	&:last-child{
		margin-bottom: 8px;
	}
}